.backdrop.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  overflow-y: auto;
  background-color: rgba(32, 13, 29, 0.6);
  transition: opacity 300ms ease-in-out, visibility 300ms ease-in-out;
  backdrop-filter: blur(4px);
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 98%;
  height: 96%;
  max-width: 280px;
  padding-top: 50px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 100px;

  overflow: auto;
  transform: translate(-50%, -50%);
  border-radius: 150px 0px;
  background-color: var(--modal-background-color);

  @include for-tablet-up {
    height: 96%;
    width: 96%;

    max-height: 845px;

    max-width: 600px;

    border-radius: 300px 0px;
    padding-top: 75px;
    padding-left: 55px;
    padding-right: 55px;
    padding-bottom: 75px;
  }

  @include for-desktop-up {
    width: 96%;
    height: 96%;
    max-width: 700px;
    max-height: 1010px;
    padding-top: 90px;
    padding-left: 100px;
    padding-right: 100px;
    padding-bottom: 80px;
  }
}

.modal-close-btn {
  position: absolute;
  right: 12px;
  top: 15px;
  background-color: transparent;
  color: var(--accent-button-color);
  border: none;
  transition: all ease-in-out 300ms;
  &:hover,
  &:focus {
    scale: 1.1;
    color: #2e009a;
  }
}
.close-svg {
  fill: currentColor;
}
.event-modal-img {
  object-fit: cover;
  margin: 0 auto 20px;
  border-radius: 100px 0;
  width: 300px;
  height: 100%;

  @include for-tablet-up {
    width: 500px;
    border-radius: 200px 0px;
    margin-bottom: 40px;
  }

  @include for-desktop-up {
    width: 100%;
    border-radius: 220px 0px;
  }
}
.description-container {
  margin-bottom: 24px;
  @include for-tablet-up {
    margin-bottom: 40px;
    display: flex;
    justify-content: space-between;
  }
}

.event-info-item {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  @include for-tablet-up {
    text-align: left;
    align-items: flex-start;
  }
}
.event-info-item:not(:last-child) {
  margin-bottom: 16px;
  @include for-desktop-up {
    margin-bottom: 20px;
  }
}
.event-info__header {
  @include font(600, 20px, 24px);
  color: var(--accent-color);
  margin-bottom: 5px;
  @include for-desktop-up {
    margin-bottom: 7px;
    @include font(600, 24px, 29px);
  }
}
.event-info__description {
  max-height: 141px;
  overflow: scroll;
  max-width: 245px;
  @include for-tablet-up {
    max-width: 287px;
  }
  @include for-desktop-up {
    max-width: 427px;
    max-height: 132px;
  }
}
.event-info__place {
}
.header__prices {
  margin-bottom: 8px;
  @include for-tablet-up {
    margin-bottom: 5px;
  }
  @include for-desktop-up {
    margin-bottom: 7px;
  }
}
.event-info {
  @include font(400, 16px, 20px);
  color: var(--primary-color);
  @include for-tablet-up {
    max-width: calc((100% - 100px) / 2);
  }
  @include for-desktop-up {
    @include font(400, 18px, 22px);
  }
}
.event-info__date,
.event-info__city {
  margin-bottom: 3px;
  @include for-desktop-up {
    margin-bottom: 5px;
  }
}
.tickets {
}
.ticket-std {
  margin-bottom: 16px;
  @include for-desktop-up {
    margin-bottom: 32px;
  }
}
.ticket-wrap {
  display: flex;
  margin-bottom: 16px;
  @include for-desktop-up {
    margin-bottom: 20px;
  }
}
.barcode {
  width: 24px;
  margin-right: 13px;
  @include for-desktop-up {
    width: 29px;
  }
}
.buy-button {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  transition: all ease 250ms;
  text-decoration: none;
  background-color: var(--accent-button-color);
  color: #fff;
  @include font(500, 16px, 20px);
  &:hover,
  &:focus {
    background-color: #2e009a;
  }
  @include for-tablet-up {
    margin-left: 0;
  }
}

.more-btn {
  display: block;
  padding: 10px 3px;
  margin-right: auto;
  margin-left: auto;
  border: 1px solid var(--accent-button-color);
  border-radius: 5px;
  background-color: transparent;
  color: var(--accent-button-color);
  @include font(500, 16px, 20px);

  @include for-tablet-up {
    padding: 10px 15px;
  }
  &:hover,
  &:focus {
    border-color: rgba(220, 86, 197, 1);
  }
}

.modal-info-link {
  text-decoration: none;
  color: var(--accent-color);
}
